"use client"
import Layout from '../layout'
import {ReactElement} from "react";
import ServicesComponent from "@/app/services/_clientComponent";

export default function Services(): ReactElement {
		return (
				<Layout
						title={`Services`}
						description={`Standard and Reliable`}
						backLink={'/'}
						lgLogoShow={false}
				>
						<ServicesComponent/>
				</Layout>
		)
}